import React, { Component } from "react";
import BootstrapTable from '../../node_modules/react-bootstrap-table-next';
import { Table } from '../../node_modules/react-bootstrap';
import { Form , Button , Col } from 'react-bootstrap';
import './ticket_page.css';


export default class NewTicket extends Component {

 constructor(props) {
  super(props)
  this.state = {
    ticket : {'id':'','department':"0",'category':"0",'lab_url':'','subject':'','description':''},
    departments : [],
    categories : []
  }
      this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
 }

   handleChange = (e) => {
  let newState = {'ticket':this.state.ticket}
  newState['ticket'][e.target.name] = e.target.value
  this.setState(newState)
 }

  handleSubmit = (e, message) => {
  e.preventDefault()

  console.log(e)


  var form_data = new FormData(document.getElementById("add_ticket"));
  form_data.append('csrfmiddlewaretoken',$('[name="csrfmiddlewaretoken"]').val())


  $.ajax({
   url: '/tickets/add_ticket/',
   type: 'POST',
       header:{
    'X-CSRFTOKEN': $('[name="csrfmiddlewaretoken"]').val()
    },
   processData: false,
    contentType: false,
    cache: false,
    enctype: 'multipart/form-data',
   data: form_data,
   success: function(data) {
      this.props.history.push('/tickets')
    }.bind(this),
   error: function(xhr, status, err) {
        console.error(status, err.toString())
        alert('Some error occurred')
   }.bind(this)
  })

  }


    componentDidMount() {
        fetch('http://127.0.0.1:8000/tickets/departments/?format=json').then((res)=>res.json().then((data)=>{
            this.setState({ departments : data });
        })).then(() => {
            fetch('http://127.0.0.1:8000/tickets/categories/?format=json').then((res)=>res.json().then((data)=>{
                this.setState({ categories : data });
            }))
        })
      }

     render() {
        return(<div className="auth-wrapper mx-auto col-md-12" style={{marginTop:10 + 'em'}}>
  <Form id="add_ticket" onSubmit={this.handleSubmit} >
    <Form.Group>
    <Form.Label column sm={2}>
      <h4>Submit a ticket</h4>
    </Form.Label>
    </Form.Group>

  <Form.Group>
    <Form.Label column sm={2}>
      Ticket
    </Form.Label>
    <Col sm={5}>
    <Form.Control type="text" name="id" onChange={this.handleChange} value={"#" + this.state.ticket.id} placeholder="ticket-id" readOnly />
    </Col>
  </Form.Group>


      <Form.Group >
    <Form.Label column sm={2}>Department</Form.Label>
      <Col sm={5}>
      <Form.Control as="select" name="department" onChange={this.handleChange} value={this.state.ticket.department.id} className="mr-sm-2" id="inlineFormCustomSelect" custom>
      <option value="0">---</option>
      {this.state.departments.map(department => (
        <option value={department.id}>{department.name}</option>
      ))}
      </Form.Control>
      </Col>
    </Form.Group>


    <Form.Group >
    <Form.Label column sm={2}>Category</Form.Label>
      <Col sm={5}>
      <Form.Control as="select" name="category" onChange={this.handleChange} value={this.state.ticket.category.id} className="mr-sm-2" id="inlineFormCustomSelect" custom>
      <option value="0">---</option>
      {this.state.categories.map(category => (
        <option value={category.id}>{category.name}</option>
      ))}
      </Form.Control>
      </Col>
    </Form.Group>

  <Form.Group >
    <Form.Label column sm={2}>
      Lab URL
    </Form.Label>
    <Col sm={5}>
      <Form.Control type="url"  name="lab_url" placeholder="Lab URL" onChange={this.handleChange} value={this.state.ticket.lab_url} />
    </Col>
  </Form.Group>

  <Form.Group >
    <Form.Label column sm={2}>
      Subject
    </Form.Label>
    <Col sm={5}>
      <Form.Control type="text" name="subject" placeholder="Subject" onChange={this.handleChange} value={this.state.ticket.subject}/>
    </Col>
  </Form.Group>

    <Form.Group>
    <Form.Label column sm={2}>
      Description
    </Form.Label>
    <Col sm={5}>
      <Form.Control as="textarea" name="description" placeholder="Description"  onChange={this.handleChange} rows={3} cols={5} value={this.state.ticket.description}/>
    </Col>
  </Form.Group>

    <Form.Group>
    <Form.Label column sm={2}>Priority</Form.Label>
      <Col sm={5}>
      <Form.Control as="select" name="priority" value={this.state.ticket.priority} onChange={this.handleChange} className="mr-sm-2" id="inlineFormCustomSelect" custom>
      <option value="0">---</option>
      <option value="1">HIGH - Production System Down</option>
      <option value="2">MED - System Impaired</option>
      <option value="3">LOW - General Guidence</option>
      </Form.Control>
      </Col>
    </Form.Group>

  <Form.Group >
    <Col sm={{ span: 1, offset: 0 }}>
      <Button type="submit">Submit</Button>
    </Col>
  </Form.Group>
</Form></div>)
  }
}