import React from 'react';
import '../../../node_modules/bootstrap/dist/css/bootstrap.css';
import './App.css';
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { Form , Button , Col } from 'react-bootstrap';

import Login from "../../components/login_component";
import ManageTickets from "../../components/manage_tickets";
import TicketDetails from "../../components/ticket_details";
import NewTicket from "../../components/new_ticket";
import Home from "../../components/home_component";

function logout_click(){
   user_logged_in = false
   $('#logout_link').css('display','none')
   $('#new_ticket_link').css('display','none')
    $.get('/accounts/logout/', (data)=>{
      console.log(data)
    })
  }

function App() {
const styles = {
  container: {
   display: 'inline',
  }
}
    let which_page;
    let show_btn ;

    if(!user_logged_in){
        which_page = <Login/>
        show_btn = "none"
    }else{
        which_page = <ManageTickets/>
        show_btn = "initial"
    }

  return (
  <Router>
    <div className="App">
      <nav className="navbar navbar-expand-lg navbar-light fixed-top">
        <div className="container">
          <Link className="navbar-brand" to={"#"}>PeerXP in React</Link>
          <div className="collapse navbar-collapse" id="navbarTogglerDemo02">
            <ul className="navbar-nav ml-auto">
              <li className="nav-item" style={{display:"inline"}}>
                <Link className="nav-link" className="container" id="logout_link"  style={{display:show_btn}} onClick={logout_click} to={"/login"}>Logout</Link>
                <Link className="nav-link" className="container" id="new_ticket_link" style={{display:show_btn}} to={"/new_ticket"}>New Ticket</Link>
             </li>
            </ul>
          </div>
        </div>
      </nav>
      <Switch>
        <Route exact path='/' component={(props) => (
            <Home {...props} user_logged_in={user_logged_in} />
          )} />
        <Route exact path='/login' component={(props) => (
            <Login {...props} user_logged_in={user_logged_in} />
          )} />
        <Route path="/tickets" component={ManageTickets} />
        <Route path="/logout" component={Login} />
        <Route exact path="/ticket_details/:ticket_id" component={TicketDetails} />
        <Route exact path="/new_ticket" component={NewTicket} />
      </Switch>
    </div></Router>
  );
}

export default App;