import React, { Component } from "react";
import Login from "./login_component";
import ManageTickets from "./manage_tickets";

export default class Home extends Component {
 constructor(props) {
  super(props)
  this.state = {
  }
 }

    render() {
        return (
            this.props.user_logged_in ? <ManageTickets {...this.props} /> : <Login {...this.props} />
        );
    }
}