import React, { Component } from "react";
import BootstrapTable from '../../node_modules/react-bootstrap-table-next';
import { Table } from '../../node_modules/react-bootstrap';
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

export default class ManageTickets extends Component {
 constructor(props) {
  super(props)
  this.state = {
    tickets : []
  }
 }

componentDidMount() {
    fetch('/tickets/tickets/?format=json').then((res)=>res.json().then((data)=>{
      this.setState({ tickets: data });
    }));
  }

     render() {
    console.log(this.state.tickets) //returns [] the first render, returns ['topic1','topic2','topic3'] on the second render;
    return(
      <div className="auth-wrapper">
    <div style={{padding : 10 + 'px'}}>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th># Ticket-ID</th>
              <th>Subject</th>
              <th>Department</th>
              <th>Priority</th>
            </tr>
          </thead>
          <tbody>
          {this.state.tickets.map(ticket => (
          <tr key={ticket['id']}>
            <td>{ticket['id']}</td>
            <td><Link to={"/ticket_details/" + ticket['id']}>{ticket['subject']}</Link></td>
            <td>{ticket['department']['name']}</td>
            <td>{ticket['priority_name']}</td>
          </tr>
        ))}
        </tbody></Table></div></div>)
  }
}